  getUser = () => {
    const userId = this.props.match.params.id;
    axios
      .get(`${process.env.REACT_APP_API_URI}/dietist/profile/${userId}`)
      .then((datos) => {
        console.log("datos", datos);
        this.setState({ foodList: datos.data });
      });
  };

  deleteFood = (comidaId, comida) => {
    const userId = this.props.match.params.id;
    console.log(comidaId, userId);
    axios
      .post(
        `${process.env.REACT_APP_API_URI}/user/food/delete/${userId}/${comida}/${comidaId}`
      )
      .then(() => {
        this.getUser();
      })
      .catch((err) => console.log(err));
  };

