import React from "react";
import dietist from "./dietist-service"; // Importamos funciones para llamadas axios a la API
const { Consumer, Provider } = React.createContext();

// HOC para crear Consumer
// el componente withAuth recibe un componente como argumento y nos devuelve un componente con el mismo componente dentro de un <Consumer /> con las propiedades user e isLoggedin (state), y los métodos login, signup y logout (this)
const withDietist = (WrappedComponent) => {
  return class extends React.Component {
    render() {
      return (
        <Consumer>
          {/* El componente <Consumer> provee un callback que recibe el "value" con el objeto Providers */}
          {({ getInfo, dietitian, createUser, addUser, userProfile }) => {
            return (
              <WrappedComponent
                getInfo={getInfo}
                dietitian={dietitian}
                createUser={createUser}
                addUser={addUser}
                userProfile={userProfile}
                {...this.props}
              />
            );
          }}
        </Consumer>
      );
    }
  };
};

// Provider
class DietistProvider extends React.Component {
  state = { dietitian: {}, isLoading: true };

  componentDidMount() {
    this.setState({ isLoading: false });
  }

  getInfo = async () => {
    try {
      let dietitian = await dietist.getInfo();
      this.setState({
        dietitian,
      });
    } catch (error) {
      console.log(error);
    }
  };

  addUser = async (id) => {
    try {
      let dietistUpdate = await dietist.add(id);
      console.log(dietistUpdate);
      this.setState({
        dietitian: dietistUpdate,
      });
      console.log("StateNew: ", this.state.dietitian);
    } catch (error) {
      console.log(error);
    }
  };

  createUser = async (user) => {
    try {
      const {
        imageUrl,
        name,
        lastName,
        email,
        genre,
        weight,
        height,
        age,
      } = user;

      const newUser = await dietist.createUser({
        imageUrl,
        name,
        lastName,
        email,
        genre,
        weight,
        height,
        age,
      });
      return newUser;
    } catch (error) {
      console.log(error);
    }
  };

  userProfile = async (id) => {
    try {
      const userProfile = await dietist.userProfile(id);
      console.log(userProfile);
      return userProfile;
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    // destructuramos isLoading, isLoggedin y user de this.state y login, logout y signup de this
    const { dietitian, isLoading } = this.state;
    const { getInfo, createUser, addUser, userProfile } = this;

    return isLoading ? (
      <div></div>
    ) : (
      // si está loading, devuelve un <div> y sino devuelve un componente <Provider> con un objeto con los valores: { isLoggedin, user, login, logout, signup}
      // el objeto pasado en la prop value estará disponible para todos los componentes <Consumer>

      <Provider
        value={{ createUser, getInfo, dietitian, addUser, userProfile }}
      >
        {this.props.children}
      </Provider>
    ); /*<Provider> "value={}" datos que estarán disponibles para todos los componentes <Consumer> */
  }
}

export { Consumer, withDietist }; //  <--	RECUERDA EXPORTAR  ! ! !

export default DietistProvider; //	<--	RECUERDA EXPORTAR  ! ! !
