import React, { Component } from "react";
import { Link } from "react-router-dom";
import { withAuth } from "../lib/AuthProvider";

class Signup extends Component {
  state = { name: "", lastName: "", proName: "", email: "", password: "" };

  handleFormSubmit = (event) => {
    event.preventDefault();
    const { name, lastName, proName, email, password } = this.state;
    console.log("Signup -> form submit", {
      name,
      lastName,
      proName,
      email,
      password,
    });
    this.props.signup({ name, lastName, proName, email, password });
  };

  handleChange = (event) => {
    const { name, value } = event.target;
    this.setState({ [name]: value });
  };

  render() {
    const { name, lastName, proName, email, password } = this.state;
    return (
      <div className="container-login-signup">
        <h1>Sign Up</h1>
        <div className="form-login-signup">
          <form onSubmit={this.handleFormSubmit}>
            <label></label>
            <input
              type="text"
              name="name"
              placeholder="name"
              value={name}
              onChange={this.handleChange}
            />

            <label></label>
            <input
              type="text"
              name="lastName"
              placeholder="last name"
              value={lastName}
              onChange={this.handleChange}
            />

            <label></label>
            <input
              type="text"
              name="proName"
              placeholder="profesional name"
              value={proName}
              onChange={this.handleChange}
            />

            <label></label>
            <input
              type="email"
              name="email"
              placeholder="e-mail"
              value={email}
              onChange={this.handleChange}
            />

            <label></label>
            <input
              type="password"
              name="password"
              placeholder="password"
              value={password}
              onChange={this.handleChange}
            />

            <input className="login-btn" type="submit" value="Signup" />
          </form>
        </div>
      </div>
    );
  }
}

export default withAuth(Signup);
