import React, { Component } from "react";
import { withDietist } from "../lib/DietistProvider";
import { Link } from "react-router-dom";
import UserForm from "../components/UserForm";

export class UserProfile extends Component {
  state = { user: {} };

  getUser = async () => {
    try {
      const user = await this.props.userProfile(this.props.match.params.id);
      this.setState({
        user,
      });
    } catch (error) {
      console.log(error);
    }
  };

  componentDidMount() {
    this.getUser();
  }

  render() {
    console.log(this.props);
    const { user } = this.state;
    return (
      <div className="container-profile">
        <div className="top-part">
          <div className="profileImg">
            <img src={user.imageUrl} alt="user-img" />
          </div>
          <div className="profile-info">
            <h1 className="profileName">
              {user.name} {user.lastName}
            </h1>
            <p>{user.email}</p>
            <p>{user.genre}</p>
          </div>
        </div>
        <div className="bottom-part">
          <div>
            <h2>Info</h2>
          </div>
          <p>Age: {user.age}</p>
          <p>Height: {user.height} cm</p>
          <p>Weight: {user.weight} Kg</p>
        </div>
        <div className="btn-diet">
          <Link to={`/UserProfile/${this.props.match.params.id}/diet`}>
            Diet
          </Link>
        </div>
      </div>
    );
  }
}

export default withDietist(UserProfile);
