import React, { Component } from "react";
import "./App.css";
import "./components/NavbarMenu.css";
import { Switch, Route } from "react-router-dom";
import AuthProvider from "./lib/AuthProvider";
import Navbar from "./components/Navbar";
import DietistProvider from "./lib/DietistProvider";

import Signup from "./pages/Signup";
import Login from "./pages/Login";
import Private from "./pages/Private";
import AnonRoute from "./components/AnonRoute";
import PrivateRoute from "./components/PrivateRoute";
import Home from "./pages/Home";
import Dashboard from "./pages/Dashboard";
import UserRegistrer from "./pages/UserRegistrer";
import UserProfile from "./pages/UserProfile";
import Food from "./components/Food";
import Diet from "./pages/Diet";

class App extends Component {
  render() {
    return (
      <AuthProvider>
        <DietistProvider>
          <div className="container">
            <Navbar />
            <Switch>
              <AnonRoute exact path="/signup" component={Signup} />
              <AnonRoute exact path="/login" component={Login} />
              <PrivateRoute exact path="/private" component={Private} />
              <PrivateRoute exact path="/" component={Home} />
              <PrivateRoute exact path="/dashboard" component={Dashboard} />
              <PrivateRoute
                exact
                path="/UserRegistrer"
                component={UserRegistrer}
              />
              <PrivateRoute
                exact
                path="/UserProfile/:id/diet"
                component={Diet}
              />
              <PrivateRoute
                exact
                path="/UserProfile/:id"
                component={UserProfile}
              />
              <PrivateRoute
                exact
                // poner id comida, id usuario, seguir ejemplo postman       path="/UserProfile/:id"
                component={UserProfile}
              />
              <PrivateRoute exact path="/food" component={Food} />
            </Switch>
          </div>
        </DietistProvider>
      </AuthProvider>
    );
  }
}

export default App;
