import axios from "axios";

class Dietist {
  constructor() {
    this.dietist = axios.create({
      baseURL: process.env.REACT_APP_API_URI,
      withCredentials: true,
    });
  }

  getInfo() {
    return this.dietist.get("/dietist/get").then(({ data }) => data);
  }
  createUser({ imageUrl, name, lastName, email, genre, weight, height, age }) {
    return this.dietist
      .post("/dietist/createUser", {
        imageUrl,
        name,
        lastName,
        email,
        genre,
        weight,
        height,
        age,
      })
      .then(({ data }) => data);
  }

  add(id) {
    return this.dietist.post(`/dietist/add/${id}`, {}).then(({ data }) => data);
  }

  userProfile(id) {
    return this.dietist.get(`/dietist/profile/${id}`).then(({ data }) => data);
  }

  deleteUser({ imageUrl, name, lastName, email, genre, weight, height, age }) {
    return this.auth
      .post("/dietist/delete/:id", {
        imageUrl,
        name,
        lastName,
        email,
        genre,
        weight,
        height,
        age,
      })
      .then(({ data }) => data);
  }
}

const axiosRequestFunctions = new Dietist();

export default axiosRequestFunctions;
