import React, { Component } from "react";
import { Route, Redirect, Link, withRouter } from "react-router-dom";
import { withDietist } from "../lib/DietistProvider";
import axios from "axios";

class UserForm extends Component {
  state = {
    imageUrl: "",
    name: "",
    lastName: "",
    email: "",
    genre: "",
    weight: "",
    height: "",
    age: "",
  };

  handleFormSubmit = async (event) => {
    event.preventDefault();
    const {
      imageUrl,
      name,
      lastName,
      email,
      genre,
      weight,
      height,
      age,
    } = this.state;

    const userCreated = await this.props.createUser({
      imageUrl,
      name,
      lastName,
      email,
      genre,
      weight,
      height,
      age,
    });
    console.log(userCreated);
    await this.props.addUser(userCreated._id);
    this.redirToHome(userCreated._id);
  };

  redirToHome = (userId) => {
    this.props.history.push(`/UserProfile/${userId}`);
  };

  handleChange = (event) => {
    const { name, value } = event.target;
    this.setState({ [name]: value });
  };

  fileOnchange = (event) => {
    //Consigue el archivo del form
    const file = event.target.files[0];
    //para enviar el objeto y añadir la imagen
    const uploadData = new FormData();
    uploadData.append("photo", file);
    axios
      .post(`${process.env.REACT_APP_API_URI}/dietist/upload/`, uploadData, {
        withCredentials: true,
      })
      .then((response) => {
        this.setState({
          imageUrl: response.data.secure_url,
          disable: false,
        });
      })
      .catch((error) => console.log(error));
  };

  render() {
    const {
      imageUrl,
      name,
      lastName,
      email,
      genre,
      weight,
      height,
      age,
    } = this.state;
    return (
      <div className="container-userform">
        <h1>Registrer User</h1>
        <div className="userform">
          <form onSubmit={this.handleFormSubmit}>
            <label></label>
            <img className="imgUser" src={this.state.imageUrl} alt="" />
            <input onChange={this.fileOnchange} type="file" />

            <label>profile image</label>
            <input
              type="text"
              name="name"
              placeholder="Name"
              value={name}
              onChange={this.handleChange}
            />

            <label></label>
            <input
              type="text"
              name="lastName"
              placeholder="Last name"
              value={lastName}
              onChange={this.handleChange}
            />

            <label></label>
            <input
              type="email"
              name="email"
              placeholder="E-mail"
              value={email}
              onChange={this.handleChange}
            />

            <br></br>
            <label></label>
            <select
              name="genre"
              value={this.state.genre}
              onChange={this.handleChange}
            >
              <option value="Female">Female</option>
              <option value="Male">Male</option>
            </select>

            <label></label>
            <input
              type="text"
              name="weight"
              placeholder="Weight"
              value={weight}
              onChange={this.handleChange}
            />

            <label></label>
            <input
              type="text"
              name="height"
              placeholder="Height"
              value={height}
              onChange={this.handleChange}
            />

            <label></label>
            <input
              type="text"
              name="age"
              placeholder="Age"
              value={age}
              onChange={this.handleChange}
            />

            <input className="login-btn" type="submit" value="Signup" />
          </form>
        </div>
      </div>
    );
  }
}

export default withRouter(withDietist(UserForm));
