import React, { Component } from "react";
import { withDietist } from "../lib/DietistProvider";
import { Link } from "react-router-dom";

class Home extends Component {
  componentDidMount() {
    this.props.getInfo();
  }

  renderAllUsers = (users) => {
    return users.map((user, i) => {
      console.log(user);
      return (
        <div key={i}>
          <Link to={`/UserProfile/${user._id}`}>
            <div className="user-card">
              <div>
                <img src={user.imageUrl} alt="" />
              </div>
              <div>
                <p>
                  {user.name} {user.lastName}
                </p>
              </div>
            </div>
          </Link>
        </div>
      );
    });
  };

  render() {
    console.log(this.props.dietitian);

    const { users } = this.props.dietitian;

    return users && users.length > 0 ? (
      <>
        <div className="container-dashboard">
          <div className="container-users">{this.renderAllUsers(users)}</div>
        </div>
      </>
    ) : (
      <div></div>
    );
  }
}

export default withDietist(Home);
